import './App.css';
import Button from './components/Button';
import Input from './components/Input';
import Output from './components/Output';
import { useState } from 'react';

function App() {
  const [output, setOutput] = useState('0');
  const [input, setInput] = useState('0');
  const operators = ['+', '-', '*', '%', '/'];

  function clickEventHandle(value) {
    if (value === "C") {
      setInput('0');
      setOutput('0');
    } else if (value === "AC") {
      if (input !== '0') {
        input.length === 1 ? setInput('0') : setInput(input.slice(0, -1));
      }
    } else if (value === "=") {
      if (!operators.includes(input.slice(-1))) {
        try {
          let result = eval(input).toString();
          if (result.length > 16) result = result.slice(0, 16);
          setOutput(result);
        } catch (error) {
          console.error('Evaluation error:', error);
          setOutput('Error');
        }
      }
      setInput('0');
    } else {
      if (operators.includes(value)) {
        if (!operators.includes(input.slice(-1))) {
          try {
            const result = eval(input).toString();
            if (result.length > 16) result = result.slice(0, 16);
            setInput(result + value);
          } catch (error) {
            console.error('Evaluation error:', error);
            setOutput('Error');
            setInput('0');
          }
        } else {
          setInput(input.slice(0, -1) + value);
        }
      } else {
        const newInput = input === '0' ? value : input + value;
        if (newInput.replace(/[^0-9]/g, '').length <= 16) {
          setInput(newInput);
        }
      }
    }
  }

  return (
    <div className='container'>
      <div className='containerFrame'>
        <Output output={output} />
        <Input input={input} />
        <Button clickEventHandle={clickEventHandle} />
      </div>  
    </div>
  );
}

export default App;
